@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Satisfy&display=swap);
 
body {
  margin: 0;
  font-family: 'Caveat', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}

* {
  margin: 0;
  padding: 0px;
}

.title-div {
  grid-area: title;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
.title {
  color: rebeccapurple;
  font-size: 2rem;
}

.main-todo-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas:
    ". title title ."
    ". section section ."
    "dailyQuote dailyQuote dailyQuote dailyQuote";
  grid-template-rows: minmax(50px, auto) 1fr minmax(105px, auto);
}
.section-todo-list {
  height: 70vh;
  border-radius: 50px;
  box-shadow: 12px 12px 24px 0 rgba(0, 0, 0, 0.2), -12px -12px 24px 0 rgba(255, 255, 255, 0.5);
  overflow: hidden;
  background: #fff;
  overflow: auto;
}

.parent-section {
  grid-area: section;
  box-shadow: inset 8px 8px 16px 0 rgba(0, 0, 0, 0.2), inset -8px -8px 16px 0 rgba(255, 255, 255, 0.4);
  margin-top: 2.5rem;
  border-radius: 50px;
  padding: 17px;
}

.dailyQuotes {
  grid-area: dailyQuote;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dailyQuotesCard {
  color: #000;
  border-radius: 10px;
  box-shadow: 12px 12px 24px 0 rgba(0, 0, 0, 0.2), -12px -12px 24px 0 rgba(255, 255, 255, 0.5);
  overflow: hidden;
  background:#fff;
  height: 60%;
  width: 50vw;
  font-size: 0.8rem;
  padding: 5px;
  font-style: italic;
}

.quote {
  color: #000;
  padding: 0.8rem;
  font-size: 1.1rem;
}

.author {
  color: rebeccapurple;
  font-size: 1.2rem;
  padding-top: 0.25rem;
}

.todo-input {
   width: 75%;
}
.add-icon {
   font-size: 1.5rem;
   margin-left: 1rem;
   cursor: pointer;
   color: rebeccapurple;
}

.input-div {
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 2.5rem;
   font-family: "Caveat", cursive;
}

.MuiFormLabel-root {
   font-size: 1.3rem !important;
   font-family: "Caveat", cursive !important;
   color: rebeccapurple !important;

}

.MuiOutlinedInput-inputMarginDense {
    font-family: 'Caveat', cursive !important;
    font-size: 1.2rem !important;
}
/* 
.MuiFormLabel-root.Mui-focused{
   color: red !important;
   outline: red;
} */

.todo-card {
    border-radius: 10px;
    box-shadow: 10px 6px 16px 0 rgba(0, 0, 0, 0.2), -12px -12px 24px 0 rgba(255, 255, 255, 0.4);
    overflow: hidden;
    background: #fff;
    width: 91%;
    display: flex;
    height: 38px;
    justify-content: space-between;
    align-items: center;
    margin: 14px auto;
}

.delete-icon {
    color: #639;
    margin-right: 1.4rem;
    cursor: pointer;
}

.todo-text {
    font-size: 1.3rem;
    margin-left: 1.4rem;
    color: #639;
}

